<template>
	<div class="Australia" @click="getDetail">
		<img :src="data.img">
		<p>{{data.name}}</p>
		<span>{{data.money}}</span>
	</div>
</template>

<script>
	export default{
		data(){ return {}},
		props:{
		  data:{
		    type:Object,
		    default:function(){
		      return {}
		    }
		  },
		  dataName:{
		    type:String,
		    default:null
		  },
		  index:{
		    type:Number,
		    default:null
		  },
		  isJump:{
		    type:Boolean,
		    default:true
		  }
		},
		components:{},
		mounted(){},
		updated(){},
		methods:{
		  getDetail(){
		    //                            '/detail/234234234?dataName=home'
		    this.isJump && this.$router.push('/detail/'+this.data._id+'?dataName='+this.dataName)
		  }
		}
	}
</script>

<style scoped>
	.Australia{width:45%;float: left;text-align: center;margin:.3rem;}
	.Australia img{width:100%;height:7rem;}
	.Australia p{font:.4rem/1rem "";}
	.Australia span{font:.4rem/1rem "";color:red;}
