<template>
	<div class="reg">
	<!-- 	<h3>登陆</h3> -->
	   <input type="file" id="file" style="display: none;">
		<div class="reg__box">
			<Title/>

			
		</div>
		<div class="reg__logo" @click="upload">
			<input type="file" id="file" style="display:none;">
		</div>
		<div class="reg__register">
			<uc-input label="账号" v-model="username"/>
			<uc-input label="密码" v-model="password"/>
<!-- 			<input type="text" v-model="username">
			<input type="text" v-model="password"> -->
		</div>
		<div class="reg__click">
			<uc-button text="注册" @click.native="reg"/>
<!-- 			<input type="button" value="注册" @click="reg" class="reg__click-input"> -->
		</div>
	</div>
</template>

<script>
	import Title from '@/components/title/title.vue'
	import UcButton from '../components/uc-button.vue'
	import UcInput from '../components/uc-input.vue'
	export default{
    data() {
      return {
        username: '',
        password: '',
        mess: ''
      }
    },
    components: {
		Title,
      'uc-button': UcButton,
      'uc-input': UcInput,
    },
    mounted() {},
    updated() {},
    methods: {
		upload(){
			let file=document.getElementById('file');
			file.click()
		},
		reg(){
			let formData = new FormData();
			formData.append('username',this.username)
			formData.append('password',this.password)
			
			let file=document.getElementById('file');
			formData.append('icon',file.files[0])
			
			this.$axios({
				url:'/api/reg',
				method:'post',
				data:formData
			}).then(
				res=>{
					if(res.data.err==0){
						console.log(res.data);
						this.$router.push('/login')
					}else{
						this.mess=res.data.msg
					}
				}
			)
		}
    }
  }
	
</script>

<style scoped>
	.reg{width:100%;}
	.reg .reg__box{width:100%;height:3rem;background:#fff;text-align: center;}
/* 	.reg .reg__box h3{font:1rem/3rem "";color:#000;width:80%;float: left;} */
	.reg .reg__logo{width: 100%;height:18rem;background:url(http://img2.imgtn.bdimg.com/it/u=371361844,4234449350&fm=26&gp=0.jpg);}
	.reg .reg__register{width:100%;height:6rem;margin:1rem;}
	.reg .reg__click{width:100%;height:3rem;background: #fff;text-align:center;}
	.reg .reg__click .reg__click-input{height:3rem;width:40%;font:1rem/3rem "";color:#000;background: red;}
</style>
