	<div class="user">
		<div class="user__box">
			<h3>我的</h3>
		</div>
		<div class="user__logo">
			<div class="user__logo--img">
				<img src="https://dss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=3675415932,4054970339&fm=26&gp=0.jpg" alt="" />
			</div>
			<div class="user__logo--span">
				<span>{{data.nikename}}</span>
			</div>
		</div>
		<div class="user__nav">
			<ul>
				<li class="te2">
					<img src="/data/imgs/Notebook.png">
					<p>我的订单</p>
				</li>
				<li class="te">
					<img src="/data/imgs/address.png">
					<p>地址管理</p>
				</li>
				<li>
					<img src="/data/imgs/Customer%20.png">
					<p>联系客服</p>
				</li>
				<li >
					<img src="/data/imgs/Help.png">
					<p>帮助中中心</p>
				</li>
				<li class="te3">
					<img src="/data/imgs/Opinion.png">
					<p>意见反馈</p>
				</li>
			</ul>
		</div>
		<div class="user__input">
			<input type="button" value="退出登陆" class="user__input--click" @click="logout">
		</div>
	</div>
</template>

<script>
	export default{
		data(){ return {
			data:{}
		}},
		components:{},
		mounted(){},
		updated(){},
		beforeRouteEnter(to, from, next) {
			//条件渲染
			//抓取token
			let token = window.localStorage.getItem('token');
	
			if(token){
		
				axios({
					url:'/api/user',
				
					headers:{
						'token':token
					},
		
				}).then(
					res => res.data.err == 0 ? next(_this => _this.data=res.data.data) : next('/login'),
				
				)
			}else{
				next('/login'),
				console.log(token)
					
			}
		},
		methods:{
			logout(){
				window.localStorage.removeItem('token')
				this.$router.push('/home')
			}
		}
	}
</script>

