<template>
	<div class="uc-seller" @click="getDetail">
		<img :src='data.img'>
		<p>{{data.name}}</p>
		<span>{{data.money}}</span>	
	</div>
</template>

<script>
	export default{
		data(){ return {}},
		props:{
			  data:{
			    type:Object,
			    default:function(){
			      return {}
			    }
			  },
			  dataName:{
			    type:String,
			    default:null
			  },
			  index:{
			    type:Number,
			    default:null
			  },
			  isJump:{
			    type:Boolean,
			    default:true
			  }
			},
			components:{},
			mounted(){},
			updated(){},
			methods:{
			  getDetail(){
			    //                            '/detail/234234234?dataName=home'
			    this.isJump && this.$router.push('/detail/'+this.data._id+'?dataName='+this.dataName)
			  }
			}
		}

</script>

<style scoped>
	.uc-seller{width:40%;height:14rem;float: left;text-align:center;margin:1rem;}
	.uc-seller img{width:100%;height:10rem;}
	.uc-seller p{font-size:.5rem;}
	.uc-seller span{font-size:1rem;color:red;}
