<template>
	<div class="login">
		<div class="login__box">
			<Title bgColor="none" :borderWidth="0" arrow="gray"></Title>
		</div>
		<div class="login__logo">
			<img src="http://img5.imgtn.bdimg.com/it/u=1657990962,1873706789&fm=26&gp=0.jpg">
		</div>
		<div class="login__input">
			<uc-input label="账号" v-model="username"/>
			<uc-input label="密码" v-model="password"/>
<!-- 			<input type="text" v-model="username">
			<input type="password" v-model="password"> -->
		</div>
		<div class="login__button">
		<!-- 	    <uc-button text="登陆" /> -->
<!-- 		<input type="button" value="登陆" @click="login"> -->
			<uc-button text="登陆" @click.native='login' />
			<br>
			{{mess}}
		</div>
	</div>
</template>

<script>
	import Title from '@/components/title/title.vue'
  import UcButton from '../components/uc-button.vue'
  import UcInput from '../components/uc-input.vue'
  
  export default {
    data() {
      return {
		  username: '',
		  password: '',
		  mess:''
	  }
    },
    components: {
		Title,
	   'uc-button':UcButton,
	   'uc-input':UcInput,
    },
    mounted() {},
    updated() {},
	methods: {
		 login() {
		   // alert(1)
		   let params = new URLSearchParams();
		   params.append('username', this.username)
		   params.append('password', this.password)
			axios({
			 url: '/api/login',
			 method: 'post',
			 data: params
		   }).then(
			 res =>{
				 if(res.data.err==0){
					 //中token跳转user
					 window.localStorage.setItem('token',res.data.token);
					 this.$router.push('/user')
				 }else{
					 this.mess=res.data.msg
				 }
			 }
	
		   )
		   
		 }
   }
  }
</script>

<style scoped>
	.login{width:100%;}
	.login .login__box{width:100%;height:3rem;background: #fff;}
/* 	.login .login__box h3{font:1rem/3rem "";color:#000;} */
	.login .login__logo{width:100%;height:12rem;}
	.login .login__logo img{width:100%;height:12rem;}
	.login .login__input{width:100%;height:6rem;padding-top:4rem;}
</style>
