<template>
	<div class="box">
		<div class="box__fiext"></div>
			<div class="box__fiext-title">
				<h3>鸭嘴兽电商</h3>
			</div>

		<div class="box__input">
			<input type="text" class="input" placeholder="请输入搜索内容"/>
			<input type="button"  value="搜索" class="box__input-buuuom"/>
		</div>
		<Banner/>
		<Ucnav/>
		<Label/>
				<Ucseller
					v-for="(item,index) of list"
					:key="item._id"
					:data="item"
					:data-name="'uc-list'"
					:index="index"
				 />
	</div>
</template>

<script>
import Banner from '@/components/banner.vue'
import Ucnav from '@/components/uc-nav.vue'
import Ucseller from '@/components/uc-seller.vue'
import Label from '@/components/Label.vue'
import axios from 'axios'
	export default{  
			name:'uc-list',
		data(){
			return {
				list:[]
			}
		},
		components:{
			Banner,Ucnav,Label,Ucseller,
		},
		mounted(){
			axios({
				url:'/api/home',
				// url:'http://localhost:3001/api/home',
				params:{_page:1,_limit:3}
			}).then(
				res=>this.list=res.data.data
			)
		},
		updated(){},
		methode:{}
	}
</script>

