		
<template>

	<div id="list">
		<div class="box">
			<div class="list__h">
				<h3>产品列表</h3>
				<Title></Title>
			</div>
		</div>
	<div class="hell">

			<van-tabs  class="box">
			  <van-tab title="婴儿奶粉">
				 <Baby
					v-for="(item,index) of list"
					:key="item._id"
					:data="item"
					:data-name="'uc-list'"
					:index="index"
				/>
			  </van-tab>
			  <van-tab title="成人奶粉">
				  <Adult
					  v-for="(item,index) of Adult"
					  :key="item._id"
					  :data="item"
					  :data-name="'uc-list'"
					  :index="index"
				  />
			</van-tab>
			  <van-tab title="保健品">
				  <Health
					  v-for="(item,index) of Health"
					  :key="item._id"
					  :data="item"
					  :data-name="'uc-list'"
					  :index="index"
				  />
			  </van-tab>
			  <van-tab title="儿童用品">
				  <children
					  v-for="(item,index) of children"
					  :key="item._id"
					  :data="item"
					  :data-name="'uc-list'"
					  :index="index"
				  />
			  </van-tab>
			  <van-tab title="美妆用户">
				  <Beauty
					  v-for="(item,index) of Beauty"
					  :key="item._id"
					  :data="item"
					  :data-name="'uc-list'"
					  :index="index"
				  />
			  </van-tab>
			  <van-tab title="澳洲超市">
				  <Australia
					v-for="(item,index) of Australia"
					:key="item._id"
					:data="item"
					:data-name="'uc-list'"
					:index="index"
				  />
			  </van-tab>
			</van-tabs>
			
		</div>
	</div>
</template>

<script>
	import Title from '@/components/title/title.vue'
	import Vue from 'vue';
	import { Tab, Tabs } from 'vant';
	Vue.use(Tab).use(Tabs);
	
	import Baby from '@/components/baby.vue'
	import Adult from '@/components/Adult.vue'
	import Health from '@/components/Health.vue'
	import children from '@/components/children.vue'
	import Beauty from '@/components/Beauty.vue'
	import Australia from '@/components/Australia.vue'

	export default{
		name:'uc-list',
		  data(){
			  return {
				  list:[],
				  Adult:[],
				  Health:[],
				  children:[],
				  Beauty:[],
				  Australia:[]
			  }
		},
		components:{
			Baby,Adult,Health,children,Beauty,Australia
		},
		mounted(){
			this.$axios({
				url:'/api/home',
				params:{_page:1,_limit:8}
			}).then(
				res=>this.list=res.data.data
			),
			this.$axios({
				url:'/api/home',
				params:{_page:3,_limit:4}
			}).then(
				res=>this.Adult=res.data.data
			)
			this.$axios({
				url:'/api/home',
				params:{_page:4,_limit:6}
			}).then(
				res=>this.Health=res.data.data
			),
			this.$axios({
				url:'/api/home',
				params:{_page:6,_limit:4}
			}).then(
				res=>this.children=res.data.data
			),
			this.$axios({
				url:'/api/home',
				params:{_page:7,_limit:4}
			}).then(
				res=>this.Beauty=res.data.data
			),
			
			this.$axios({
				url:'/api/home',
				params:{_page:6,_limit:1}
			}).then(
				res=>this.Australia=res.data.data
			)
		},
		updated(){},
		methode:{},
		methods: {}

	}
</script>

<style scoped>
	#list{width:100%;}
	#list .box{width:100%;height:4rem;background: #fff;}
	#list .box .list__h{width: 100%;height:4rem;background: #fff;position: fixed;top:0;z-index: 99999;}
	#list .list__h h3{font:1rem/4rem "";color:#000;width:100%;height:2rem;background: #fff;text-align: center;position: fixed;top:0;z-index: 99;}
	#list .hell{width:100%;float: left;padding-left:.5rem;}
	#list .hell ul{width:90%;overflow: hidden;float:left;padding-bottom:4rem;display: none;}
	#list .hell ul li{float:left;width:45%;height:12rem;text-align: center;margin:1rem;background:#ccc;}
	#list .hell ul li img{width:100%;height:7rem;}
	#list .hell ul li p{font:.4rem/1rem "";}
	#list .hell ul li span{font:.4rem/1rem "";color:red;}
	#list .hell ul:nth-child(1){display: block;}
</style>
